<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <div style="margin-top: 15px">
        <el-row :gutter="20">
          <el-col :span="10">
            <el-input
              placeholder="请输入内容"
              v-model="queryInfo.query"
              clearable
              @clear="this.getUsers"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="this.getUsers"
              >
              </el-button>
            </el-input>
          </el-col>
          <el-col :span="3"
            ><el-button type="primary" @click="addDialog">
              添加用户
            </el-button>
          </el-col>
        </el-row>
        <!-- 添加用户弹出框 -->
        <el-dialog
          title="添加用户"
          :visible.sync="dialogVisible"
          width="50%"
          @close="userReset"
        >
          <!-- 内容区域 -->

          <el-form
            ref="addUserRef"
            label-width="100px"
            class="demo-ruleForm"
            :rules="rules"
            :model="addUser"
          >
            <el-form-item label="用户名" prop="username">
              <el-input v-model="addUser.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="addUser.password" type="password"></el-input>
            </el-form-item>
            <el-form-item label="用户邮箱" prop="email">
              <el-input v-model="addUser.email"></el-input>
            </el-form-item>
            <el-form-item label="用户电话" prop="mobile">
              <el-input v-model="addUser.mobile"></el-input>
            </el-form-item>
          </el-form>

          <!-- 底部操作区域 -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleClose">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 表格区域 -->
        <template>
          <el-table :data="users" border style="width: 100%">
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="username" label="姓名"></el-table-column>
            <el-table-column prop="email" label="邮箱"></el-table-column>
            <el-table-column prop="mobile" label="电话"> </el-table-column>
            <el-table-column prop="role_name" label="角色"></el-table-column>
            <el-table-column prop="mg_state" label="状态">
              <template slot-scope="scope">
                <el-switch
                  v-model="scope.row.mg_state"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  @change="changeState(scope.row)"
                >
                </el-switch>
              </template>
            </el-table-column>
            <!-- 修改用户信息按钮 -->
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  circle
                  @click="editing(scope.row.id)"
                >
                </el-button>
                <!-- 修改用户弹出层 -->
                <el-dialog
                  title="添加用户"
                  :visible.sync="editVisible"
                  width="50%"
                >
                  <el-form
                    ref="editUserRef"
                    label-width="100px"
                    class="demo-ruleForm"
                    :rules="rules"
                    :model="editUser"
                  >
                    <el-form-item label="用户名">
                      <el-input v-model="editUser.username" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="用户邮箱" prop="email">
                      <el-input v-model="editUser.email"></el-input>
                    </el-form-item>
                    <el-form-item label="用户电话" prop="mobile">
                      <el-input v-model="editUser.mobile"></el-input>
                    </el-form-item>
                  </el-form>
                  <!-- 底部操作区域 -->
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="editVisible = false">取 消</el-button>
                    <el-button type="primary" @click="editClose(scope.row.id)">
                      确 定
                    </el-button>
                  </span>
                </el-dialog>
                <!-- 删除用户按钮 -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  @click="delVisible(scope.row.id)"
                >
                </el-button>
                <el-tooltip effect="dark" content="分配角色" placement="top">
                  <!-- 分配角色按钮 -->
                  <el-button
                    type="success"
                    icon="el-icon-s-tools"
                    circle
                    @click="editRoles(scope.row)"
                  >
                  </el-button>
                </el-tooltip>
                <!-- 分配角色弹出层 -->
                <el-dialog
                  title="分配角色"
                  :visible.sync="roleVisible"
                  width="30%"
                  :before-close="handleClose"
                  @close="closeDialog"
                >
                  <p><b>用户名 :</b>{{ userlist.username }}</p>
                  <p><b>用户角色 : </b>{{ userlist.role_name }}</p>
                  <p>
                    角色分配:
                    <!-- 下拉菜单 -->
                    <template>
                      <el-select v-model="selectRoleId" placeholder="请选择">
                        <el-option
                          v-for="item in roleList"
                          :key="item.id"
                          :label="item.roleName"
                          :value="item.id"
                        >
                        </el-option>
                      </el-select>
                    </template>
                  </p>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="roleVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submitRoles(scope.row.id)"
                      >确 定</el-button
                    >
                  </span>
                </el-dialog>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <!-- 分页区域 -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="+queryInfo.pagenum"
            :page-sizes="[1, 2, 3, 5]"
            :page-size="+queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="+total"
          >
          </el-pagination>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    //手机号验证规则
    var validateNumber = (rule, value, cb) => {
      const regMobile = /^(13[0-9]|14[01456879]|15[0-3,5-9]|16[2567]|17[0-8]|18[0-9]|19[0-3,5-9])\d{8}$/

      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error('手机号不符合规范'))
    }
    return {
      //用户列表数据
      users: [],
      //页码数据
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      //总用户数量
      total: '',
      //控制添加用户弹出层
      dialogVisible: false,
      //添加用户表单数据
      addUser: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      //修改用户表单数据
      editUser: {},
      //控制修改用户弹出层
      editVisible: false,
      //控制分配角色弹出层
      roleVisible: false,
      //点击渲染分配角色信息
      userlist: {},
      //渲染分配角色选项
      roleList: [],
      //选中的id
      selectRoleId: '',
      //表单验证规则
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          {
            type: 'email',
            message: '请输入正确的邮箱地址',
            trigger: ['blur', 'change']
          }
        ],
        mobile: [
          { required: true, message: '号码不能为空' },
          {
            validator: validateNumber,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    if (!window.sessionStorage.getItem('pagesize')) return
    this.queryInfo.pagesize = window.sessionStorage.getItem('pagesize')
  },
  mounted() {
    this.getUsers()
  },
  methods: {
    //获取用户信息
    async getUsers() {
      const res = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200)
        return this.$message.error('获取管理员列表失败')
      this.users = res.data.users
      this.total = res.data.total
    },
    //切换每页显示数量
    handleSizeChange(val) {
      this.queryInfo.pagesize = val
      window.sessionStorage.setItem('pagesize', val)
      this.getUsers()
    },
    //切换分页
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val
      this.getUsers()
    },
    //监听用户状态的改变
    async changeState(val) {
      const res = await this.$http.put(`users/${val.id}/state/${val.mg_state}`)

      if (res.meta.status !== 200) {
        this.$message.error('用户状态更新失败!')
        this.users.mg_state = !this.users.mg_state
      }
      this.getUsers()
      this.$message.success('更新用户状态成功')
    },
    //提交添加用户的信息
    handleClose() {
      this.$refs.addUserRef.validate(async valid => {
        if (!valid) return
        const res = await this.$http.post('users', this.addUser)
        if (res.meta.status !== 201) return this.$message.error('添加失败!')
        this.dialogVisible = false
        this.$message.success('添加成功!')
        this.getUsers()
      })
    },
    //点击添加用户,显示弹出层
    addDialog() {
      this.dialogVisible = true
    },
    //关闭弹出层时清空表单数据
    userReset() {
      this.$refs.addUserRef.resetFields()
    },
    //点击修改用户信息按钮,显示弹出层
    async editing(id) {
      this.editVisible = true
      const res = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('加载用户信息失败!')
      }

      this.editUser = res.data
    },
    //提交修改信息
    async editClose(id) {
      const res = await this.$http.put('users/' + id, this.editUser)

      if (res.meta.status !== 200)
        return this.$message.error('修改用户信息失败!')

      this.$message.success('修改用户信息成功!')
      this.editVisible = false
      this.getUsers()
    },
    //删除用户功能
    delVisible(id) {
      this.$confirm('删除后将无法恢复, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$http.delete('users/' + id)
          if (res.meta.status !== 200) {
            return this.$message.error('删除失败!')
            this.$message.success('删除成功!')
            this.getUsers()
          }
        })
        .catch(() => {
          this.$message({
            type: 'error',
            message: '已取消删除'
          })
        })
    },
    //点击分配角色,渲染用户信息
    async editRoles(role) {
      const res = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('角色列表加载失败')
      }
      this.roleList = res.data
      this.roleVisible = true
      this.userlist = role
    },

    //当dialog关闭时清空角色分配选项
    closeDialog() {
      this.selectRoleId = ''
    },
    //点击提交角色分配结果
    async submitRoles(id) {
      const res = await this.$http.put(`users/${id}/role`, {
        rid: this.selectRoleId
      })
      if (res.meta.status !== 200) {
        return this.$message.error('角色分配失败!')
      }
      this.$message.success('角色分配成功!')
      this.getUsers()
      this.roleVisible = false
    }
  }
}
</script>
<style lang="css" scoped></style>
